/* Footer */
footer {
  background: #8DACD4;
  height: 164px;
  margin-top: 8px;
  text-align: center;
}

.icons i {
  color: #fff;
  height: 16vh;;
  line-height: 16vh;
  padding: 12px;
}

footer .icons i:hover {
  color: #72532b;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}


h1.texto-nombre {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 0;
}

.text-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.presentacion-wrapper {
  background: url(/test-github-react/static/media/home.0f71ecec.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.presentacion {
  background: #fff;
  height: 600px;
  opacity: 0.6;
  padding: 2px;
  position: relative;
  text-align: center;
}

.presentacion h2 {
  color: #72532b;
  font-size: 2rem;
  margin-top: 0;
}


/* Portafolio link */
.portafolio-link {
  margin-top: 10rem;
  text-align: center;
}

.p-link {
  text-decoration: underline;
  margin-bottom: 0;
}

/* Work Done */
.portafolio {
  margin-top: 5rem;
}

.portafolio-titulo {
  text-align: center;
}

.work-info {
  padding: 6px;
  margin: auto;
  max-width: 400px;
}

.work-info h3 {
  text-align: center;
}

.work {
  border: 1px solid #898989;
  border-radius: 12px;
  max-width: 450px;
}

.work p {
  margin: 8px 0;
  font-size: 16px;
}

.trabajos-hechos {
    display: grid;
    grid-column-gap: 50px;
    grid-row-gap: 50px;
    grid-template-columns: auto auto;
}

.snippet {
  background: #8DACD4;
  border: 2px solid #8DACD4;
  border-radius: 12px;
  margin: auto;
  max-width: 450px;
  height: 230px;
}

.snippet img {
  border-radius: 12px;
  height: 225px;
  width: 100%;
}

@media screen and (max-width: 600px) {
  .work p, h1, h2, h3, li {
    font-size: 0.8em;
  }
  .work-info {
    padding: 4px;
  }
}

* {
  box-sizing: border-box;
}


html, body {
  height: 100%;
  width: 100%;
  margin: 0;
}

html {
    font-family: 'Open Sans', sans-serif;
}

.App, #root {
  height: 100%;
}

a {
  text-decoration: none;
}

p {
    font-size: 1.2rem;
}

.container {
  width: 80%;
  margin: 0 auto -164px auto;
  min-height: 100%;
}

/* This is to create sticky footer */
.container::after {
  content: "";
  display: block;
  height: 164px;
}

.container::after {
}

/* Add a black background color to the top navigation */
.topnav {
  background-color: #8DACD4;
  overflow: hidden;
}
  
/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700; 
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  color: #72532b;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  text-decoration: underline;
}

/* Add an active class to highlight the current page */
a.active {
  color: #72532b;
}

/* Hide the link that should open and close the topnav on small screens */
.topnav .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  p, h1, h2, h3 {font-size: 0.8em;}
  .topnav a:not(:first-child) {display: none;}
  .topnav a.icon {
    float: right;
    display: block;
  }
}

/* The "responsive" class is added to the topnav with JavaScript when the user clicks on the icon. This class makes the topnav look good on small screens (display the links vertically instead of horizontally) */
@media screen and (max-width: 600px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }

  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }

  .trabajos-hechos {
    display: grid;
    grid-column-gap: 50px;
    grid-row-gap: 50px;
    grid-template-columns: auto;
  }
}

/*** Blog, contacto, acerca reset width CSS ***/
.blog, .contacto, .acerca {
  width: 60%;
}

