
h1.texto-nombre {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 0;
}

.text-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.presentacion-wrapper {
  background: url("../img/home.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.presentacion {
  background: #fff;
  height: 600px;
  opacity: 0.6;
  padding: 2px;
  position: relative;
  text-align: center;
}

.presentacion h2 {
  color: #72532b;
  font-size: 2rem;
  margin-top: 0;
}


/* Portafolio link */
.portafolio-link {
  margin-top: 10rem;
  text-align: center;
}

.p-link {
  text-decoration: underline;
  margin-bottom: 0;
}

/* Work Done */
.portafolio {
  margin-top: 5rem;
}

.portafolio-titulo {
  text-align: center;
}

.work-info {
  padding: 6px;
  margin: auto;
  max-width: 400px;
}

.work-info h3 {
  text-align: center;
}

.work {
  border: 1px solid #898989;
  border-radius: 12px;
  max-width: 450px;
}

.work p {
  margin: 8px 0;
  font-size: 16px;
}

.trabajos-hechos {
    display: grid;
    grid-column-gap: 50px;
    grid-row-gap: 50px;
    grid-template-columns: auto auto;
}

.snippet {
  background: #8DACD4;
  border: 2px solid #8DACD4;
  border-radius: 12px;
  margin: auto;
  max-width: 450px;
  height: 230px;
}

.snippet img {
  border-radius: 12px;
  height: 225px;
  width: 100%;
}

@media screen and (max-width: 600px) {
  .work p, h1, h2, h3, li {
    font-size: 0.8em;
  }
  .work-info {
    padding: 4px;
  }
}
